
const errorMessageStyle = { color: "yellow", fontSize: "12px" };
import AMapLoader from "@amap/amap-jsapi-loader";
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { GlobalDataProps } from "../store/index";
import useMousePosition from "../hooks/useMousePosition";
import nipplejs from "nipplejs";
import tsx from "../components/tsx";
import { http } from "wmc-components";

const fontFamilyArr = [
	{ text: "宋体", value: '"SimSun","STSong"' },
	{ text: "黑体", value: '"SimHei","STHeiti"' },
	{ text: "楷体", value: '"KaiTi","STKaiti"' },
	{ text: "仿宋", value: '"FangSong","STFangsong"' },
];
export default defineComponent({
	name: "Home",
	components: { tsx },
	setup() {
		const joyRef = ref();
		const img = ref();
		http({
			url: "https://www.baidu.com",
		});
		onMounted(() => {
			const options: object = {
				mode: "semi", // 'dynamic', 'static' or 'semi'
				size: 150,
				position: {
					left: "150px",
					top: "150px",
				}, //在容器内垂直居中显示
				zone: joyRef.value, //如果不提提供zone容器元素，那么默认动态生成的元素是注入在body中的。
			};
			nipplejs.create(options);
		});

		AMapLoader.load({
			key: "2b4552a40e781712b959acbc94c917c9", // 申请好的Web端开发者Key，首次调用 load 时必填
			version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
			plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
			AMapUI: {
				version: "1.1",
				plugins: [],
			},
			Loca: {},
		})
			.then((AMap) => {
				const map = new AMap.Map("container");
			})
			.catch((e) => {
				console.log(e);
			});
		const position = useMousePosition();
		const store = useStore<GlobalDataProps>();
		const components = computed(() => {
			return store.state.editor.components;
		});
		const formSubmitResult = (result: boolean) => {
			console.log(result);
		};
		const inputValueRef = ref("wmc");
		const inputRules = [
			{
				type: "required",
				message: "用户名不能为空",
			},
			{
				type: "range",
				min: { length: 6, message: "用户名不能少于六位" },
			},
			{
				type: "email",
				message: "邮箱格式不正确",
			},
			{
				type: "custom",
				validator: () => {
					if (inputValueRef.value == "1006004868@qq.com") {
						return true;
					} else {
						return false;
					}
				},
				message: "不是我的邮箱",
			},
		];
		return {
			inputValueRef,
			inputRules,
			components,
			position,
			formSubmitResult,
			errorMessageStyle,
			joyRef,
			fontFamilyArr,
			img,
		};
	},
});
